.header {
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    position: sticky;
    background-color: white;
    z-index:0;
    top:0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75)

}
.header_left{
    display:flex;
   justify-content: space-evenly;
}

.header_input{
    display: flex;
    align-items: center;
    background-color:#eff2f5;
    padding:10px;
    margin-left: 10px;
    border-radius: 999px;

}

.header_left img{
    height: 40px;
width: 40px
}

.header_input input{
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.header_center{
    display:flex;
    flex: 1;
    justify-content: center;
}

.header_option .MuiSvgIcon-root{
    color:gray
}

.header_option:hover .MuiSvgIcon-root{ /* changed the color of the image */
    color: #2e81f4
}

.header_option-active > .MuiSvgIcon-root{
    color: #2e81f4
}
.header_option-active{
    border-bottom: 4px solid #2e81f4
}

.header_option{
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.header_option:hover{ /*changes the div in which the image is in*/
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0 30px;
    border-bottom: none;
}

.header_info {
    display: flex;
    align-items: center;
}

.header_info h4{
    margin-left: 10px;
}
.header_right{
    display: flex
}
